<template>
  <div>
    <div class="look">
      <div class="box"
           ref="box"
           :style="{transform:boxXdeg}"
      >
        <div class="span spanA"
             ref="spanA"
             :style="{ transform:spansDeg('spaA')}"
        ></div>
        <div class="span spanB" ref="spanB"
             :style="{transform:spansDeg('spaB')}"></div>
        <div class="span spanC" ref="spanC"
             :style="{transform:spansDeg('spaC')}"></div>
        <div class="span spanD" ref="spanD"
             :style="{transform:spansDeg('spaD')}"></div>

        <div class="span spanE" ref="spanE"
             :style="{transform:spansDeg('spaE')}"></div>
        <div class="span spanF" ref="spanF"
             :style="{transform:spansDeg('spaF')}"></div>
        <div class="span spanG" ref="spanG"
             :style="{transform:spansDeg('spaG')}"></div>
        <div class="span spanH" ref="spanH"
             :style="{transform:spansDeg('spaH')}"></div>
      </div>
    </div>
    <div class="controlBtn">
      <button class="leftt">left</button>
      <button class="topl" @click="toplFn">top</button>
      <button class="topr" @click="toprFn">top</button>
      <button class="rightt" @click="righttFn">right</button>
      <br/>

      <button class="leftb">left</button>
      <button class="boottoml" @click="bottomlFn">bottom</button>
      <button class="bottomr" @click="bottomrFn">bottom</button>
      <button class="rightb">right</button>
    </div>

    <div class="control">
      <div>boxX:<input type="range" max="120" min="-120" v-model="boxX"> {{boxX}}</div>
      <div>boxY: <input type="range" max="120" min="-120" v-model="boxY">{{boxY}}</div>
      <div>boxZ: <input type="range" max="120" min="-120" v-model="boxZ">{{boxZ}}</div>
    </div>
    <div class="spanTip">
      <div v-for="(item,key,index) in spanTips">{{key}}
        <input type="range" max="360" min="-360" v-model="item.x"><span>x:{{item.x}}</span>
        <input type="range" max="360" min="-360" v-model="item.y"><span>y:{{item.y}}</span>
        <input type="range" max="360" min="-360" v-model="item.z"><span>z:{{item.z}}</span>
      </div>
    </div>
  </div>
</template>

<script>

  import MakeTh from '../three/three.js'
  export default {
    name: "rubik",
    data(){
      return {
        boxX:-20,
        boxY:-20,
        boxZ:0,
        spanTips:{
          spaA:{
            x:0,y:0,z:0,tlZ:-50
          },
          spaB:{
            x:0,y:0,z:0,tlZ:-50
          },
          spaC:{
            x:0,y:0,z:0,tlZ:-50
          },
          spaD:{
            x:0,y:0,z:0,tlZ:-50
          },
          spaE:{
            x:0,y:0,z:0,tlZ:50
          },
          spaF:{
            x:0,y:0,z:0,tlZ:50
          },
          spaG:{
            x:0,y:0,z:0,tlZ:50
          },
          spaH:{
            x:0,y:0,z:0,tlZ:50
          }
        },
        tsfOrigin:{
          0:{
            x:100,y:100,z:-50
          },
          1:{
            x:0,y:100,z:-50
          },
          2:{
            x:100,y:0,z:-50
          },
          3:{
            x:0,y:0,z:-50
          },
          4:{
            x:0,y:100,z:50
          },
          5:{
            x:100,y:100,z:50
          },
          6:{
            x:0,y:0,z:50
          },
          7:{
            x:100,y:0,z:50
          },
        },
        front:['spaH','spaG','spaF','spaE','spaC','spaD','spaA','spaB']
        //-----0------1-------2-------3-----4--------5------6------7
      }
    },
    computed:{
      boxXdeg(){
        return `rotateX(${this.boxX}deg) rotateY(${this.boxY}deg)  rotateZ(${this.boxZ}deg) `
      }
    },
    methods:{
      setInner(obj,txt){
        let arr=obj.getElementsByTagName('div');
        for(let i of arr){
          i.innerHTML=txt;
        }
      },
      spansDeg(str){
        const t=this.spanTips[str];
        return `translateZ(${t.tlZ}px) rotateX(${t.x}deg) rotateY(${t.y}deg)  rotateZ(${t.z}deg) `
      },
      changeArr(a,b,num,ixy,ifl){ //b=>要改的
        if(ifl){
          this.spanTips[a][ixy]+=num;
          this.front[b]=a;
        }else{
          this.spanTips[this.front[a]][ixy]+=num;
          this.front[b]=this.front[a];
        }
      },
      toprFn(){
        let a=this.front[1];
        this.changeArr(3,1,90,'x');
        this.changeArr(6,3,90,'x');
        this.changeArr(4,6,90,'x');
        this.changeArr(a,4,90,'x',1);
        this.setTsfOr();
      },
      toplFn(){
      //  front:['spaH','spaG','spaF','spaE','spaC','spaD','spaA','spaB']
            //-----0------1-------2-------3-----4--------5------6------7
      },
      setTsfOr(){
        for(let i in this.front){
          switch (this.front[i].charAt(3)) {
            case 'H':
              this.setTsf('spanH',i);
              break;
            case 'G':
              this.setTsf('spanG',i);
              break;
            case 'F':
              this.setTsf('spanF',i);
              break;
            case 'E':
              this.setTsf('spanE',i);
              break;
            case 'C':
              this.setTsf('spanC',i);
              break;
            case 'D':
              this.setTsf('spanD',i);
              break;
            case 'A':
              this.setTsf('spanA',i);
              break;
            case 'B':
              this.setTsf('spanB',i);
              break;
          }
        }
      },
      setTsf(str,i){
        console.log(str+":::"+i);
        console.log(this.tsfOrigin[i].x);
        this.$refs[str].style.transformOrigin=`${this.tsfOrigin[i].x}px
                                               ${this.tsfOrigin[i].y}px
                                               ${this.tsfOrigin[i].z}px`;
      },
      righttFn(){
        console.log('a');
        this.setTsfOr();
        let a=this.front[1];
        this.changeArr(0,1,90,'y');
        this.changeArr(5,0,90,'y');
        this.changeArr(4,5,90,'y');
        this.changeArr(a,4,90,'y',1);

      },
      bottomrFn(){
        this.setTsfOr();
        let a=this.front[1];
        this.changeArr(4,1,-90,'x');
        this.changeArr(6,4,-90,'x');
        this.changeArr(3,6,-90,'x');
        this.changeArr(a,3,-90,'x',1);

        console.log(this.front);
      },
      bottomlFn(){
      },
    },
    mounted() {
      new MakeTh(this.$refs.spanA);
      new MakeTh(this.$refs.spanB);
      new MakeTh(this.$refs.spanC);
      new MakeTh(this.$refs.spanD);
      new MakeTh(this.$refs.spanE);
      new MakeTh(this.$refs.spanF);
      new MakeTh(this.$refs.spanG);
      new MakeTh(this.$refs.spanH);

      this.setInner(this.$refs.spanA,'A');
      this.setInner(this.$refs.spanB,'B');
      this.setInner(this.$refs.spanC,'C');
      this.setInner(this.$refs.spanD,'D');
      this.setInner(this.$refs.spanE,'E');
      this.setInner(this.$refs.spanF,'F');
      this.setInner(this.$refs.spanG,'G');
      this.setInner(this.$refs.spanH,'H');

      this.setTsfOr();
    }
  }
</script>

<style scoped>

  .spanTip span{
    display: inline-block;
    width: 40px;
    height: 20px;
    text-align: center;
    line-height: 20px;
    font-size: 14px;
  }
  .spanTip input{
    width: 100px;
  }
  .controlBtn button{
    display: inline-block;
    width: 80px;
    height: 50px;
  }
  .control input{
    width: 300px;
  }
  .controlBtn{
    margin-top: 10px;
    margin-left: 30px;
  }


  .look{
    perspective: 0;
  }
  .box{
    width: 208px;
    height: 208px;
    margin: 100px auto;
    position: relative;
    transform-style: preserve-3d;
    /*transform: rotateX(-30deg) rotateY(-30deg) rotateZ(0deg);*/
  }
  .span{
    position: absolute;
    top: 0;
    left: 0;
    /*transition: all 5s;*/
  }
  .spanA{
    top: 100px;
    left: 100px;
    transform-origin: 0px 0px 50px;
    transform:translateZ(-50px) rotateX(0deg) rotateY(0deg) rotateZ(0deg);
  }
  .spanB{
    top: 100px;
    transform-origin: 100px 0px 50px;
    transform:translateZ(-50px) rotateX(0deg) rotateY(0deg) rotateZ(0deg);
  }
  .spanC{
    left: 100px;
    transform-origin: 0 100px 50px;
    transform:translateZ(-50px) rotateX(0deg) rotateY(0deg) rotateZ(0deg);
  }
  .spanD{
    transform-origin: 100px 100px 50px;
    transform:translateZ(-50px)  rotateX(0deg) rotateY(0deg) rotateZ(0deg);
  }

  .spanE{
    top: 100px;
    left: 100px;
    transform-origin: 0px 0px -50px;
    transform:translateZ(50px) rotateX(0deg) rotateY(0deg) rotateZ(0deg);
  }
  .spanF{
    top: 100px;
    transform-origin: 100px 0px -50px;
    transform:translateZ(50px) rotateX(0deg) rotateY(0deg) rotateZ(0deg);
  }
  .spanG{
    left: 100px;
    transform-origin: 0 100px -50px;
    transform:translateZ(50px) rotateX(0deg) rotateY(0deg) rotateZ(0deg);
  }
  .spanH{
    transform-origin: 100px 100px -50px;
    transform:translateZ(50px) rotateX(0deg) rotateY(0deg) rotateZ(0deg);
  }
</style>
